/* eslint-disable jsx-a11y/label-has-for */

import { Container } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import { withStyles } from '@material-ui/core/styles';
import CameraIcon from '@material-ui/icons/CameraAltOutlined';
import PropTypes from 'prop-types';
import React from 'react';

const styles = {
  container: {
    marginTop: 0,
    paddingTop: 0,
    height: '100vh',
    width: '100vw',
    backgroundColor: '#eee',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  captureInput: {
    display: 'none',
  },
};

class Index extends React.PureComponent {
  render() {
    console.log('render');
    const {
      classes: {
        container,
        captureInput,
      },
      extract,
      extractedImage,
    } = this.props;

    const onClickCapture = event => {
      console.log('abc');
      const reader = new FileReader();
      reader.onloadend = () => {
        console.log(reader.result);
        extract(reader.result.split(',').pop());
      };
      reader.readAsDataURL(event.target.files[0]);
    };

    console.log(extractedImage);
    if (!extractedImage) {
      return (
        <Container className={container}>
          <input
            className={captureInput}
            accept="image/*"
            id="capture"
            type="file"
            multiple="multiple"
            // capture="camera"
            onChange={onClickCapture}
          />
          <label htmlFor="capture">
            <Fab
              variant="contained"
              color="default"
              component="span"
              size="large"
              aria-label="Add"
            >
              <CameraIcon />
            </Fab>
          </label>
        </Container>
      );
    }

    return (
      <Container className={container}>
        <img src={`data:image/png;base64, ${extractedImage}`} alt="" />
      </Container>
    );
  }
}

Index.propTypes = {
  classes: PropTypes.shape({
    container: PropTypes.shape({}).isRequired,
    captureInput: PropTypes.shape({}).isRequired,
  }).isRequired,
  extract: PropTypes.func.isRequired,
  extractedImage: PropTypes.string.isRequired,
};

Index.defaultProps = {
};

export default withStyles(styles)(Index);
